.button {
    cursor: pointer;
    font: inherit;
    border: none;
    background-color: #fff;
    color: black;
    padding: 0.75rem 3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 25px;
    font-weight: bold;
    transition: all .3s ease-in-out;
    margin-left: 1rem;
  }
  
  .button:hover,
  .button:active {
    background-color: #F7E2E2;
    box-shadow: 0 8px 8px rgba(254, 254, 254, 0.25);
    transform: scale(1.1);
  }
  
  .icon {
    width: 1.35rem;
    height: 1.35rem;
    margin-right: 0.5rem;
  }
  
  .badge {
    background-color: #595B83;
    padding: 0.25rem 1rem;
    border-radius: 25px;
    margin-left: 1rem;
    font-weight: bold;
    color: white;
  }
  
  .button:hover .badge,
  .button:active .badge {
    background-color: #333456;
  }
  
  .bump {
    animation: bump 300ms ease-out;
  }
  
  @keyframes bump {
    0% {
      transform: scale(1);
    }
    10% {
      transform: scale(0.9);
    }
    30% {
      transform: scale(1.1);
    }
    50% {
      transform: scale(1.15);
    }
    100% {
      transform: scale(1);
    }
  }